import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'

import { Link, graphql, useStaticQuery } from 'gatsby'

// Create React componenet
// A valid React componenet must return a ONLY React element
const ProjectsPage = () => {
  const data = useStaticQuery(graphql`
    query {
      allContentfulProject(limit: 4) {
        edges {
          node {
            id
            slug
            title
            description
            technologiesUsed
          }
        }
      }
    }
  `)
  const {
    allContentfulProject: { edges: projects },
  } = data

  return (
    <Layout>
      {/*START CONTENT*/}
      <SEO title="Projects" description='My Projects' />

      <ol>
        {projects.map(({ node: project }) => {
          return (
            <Link to={`/project/${project.slug}`}>
              <li key={project.id}>
                <h4>{project.title}</h4>
                <small>{project.technologiesUsed}</small>
                <p>{project.description}</p>
              </li>
            </Link>
          )
        })}
      </ol>

      {/*END CONTENT*/}
    </Layout>
  )
}

export default ProjectsPage
